import './Login.css'
import {Paper } from '@material-ui/core';
import { Container, Navbar,Nav, Form, Row, Col, Button } from "react-bootstrap";
import { Link, useNavigate,useParams } from 'react-router-dom';
import axios from 'axios'
import { useEffect, useState } from 'react';
import NavbarHeader from '../Navbar/Navbar';
export default function ResetPassword(){
  let navigate = useNavigate();
  const [email,setEmail]=useState({
  email:""
})
const  {token}= useParams();
const handleChange=(e)=>{
    console.log(e.target.value);
    const {name,value}=e.target;
     setEmail({...email,[name]:value})
    }
    const reset=(e)=>{
      axios.post("/api/reset-password",{
          email:email.email
      }).then((response)=>{
          console.log("Forgot password iss"+response);
      })
      alert("check your email")

    }
  
  
  return(
    <div>
        <NavbarHeader/>
 <Container>
      <Row>
        <Col lg={4}/>
        <Col lg={4}  className="login_Box">
        <Form>
  <Form.Group className="mb-3" controlId="formBasicEmail">
    {/* <Form.Label>Email address</Form.Label> */}
    <Form.Control type="email" placeholder="Enter your email" name="email" onChange={handleChange} value={email.email} required/>
  </Form.Group>
  <Button variant="primary" type="submit" onClick={reset}>
    Send Email
  </Button>
</Form>
        </Col>
        <Col lg={4}/>
      </Row>
    </Container>
 <Paper id="footer">Kerala Public School&copy;Pvt Ltd</Paper>
     </div>
   

  )
}