import { Button, Paper } from '@material-ui/core';
import { useState } from 'react';
import { Container, Navbar,Nav, Form, Row, Col } from "react-bootstrap";
import axios from 'axios';
import "toastify-js/src/toastify.css"
import Toastify from 'toastify-js'
import { Link, useNavigate } from 'react-router-dom';
import NavbarHeader from '../../Navbar/Navbar';
export default function TeacherRegister(){
    let navigate = useNavigate();
    const [add,setAdd]=useState({
        name:"",
        joining_date:"",
        mobile:"",
        email:"",
        address:"",
        password:""
    })
    const handleChange=(e)=>{
        console.log(e.target.value);
        const {name,value}=e.target;
         setAdd({...add,[name]:value})
        }
        const Add=(e)=>{
            e.preventDefault() 
            axios.post("/api/admin/teacherRegister",{
                name:add.name,
                joining_date:add.joining_date,
                mobile:add.mobile,
                email:add.email,
                address:add.address,
                password:add.password
            })
         .then((response)=>{console.log("backend data"+response)
         if(response.data=="error"){
          Toastify({
            text: "User with same email exists....",
            duration: 5000,
            newWindow: true,
            close: true,
            stopOnFocus: true, // Prevents dismissing of toast on hover
            offset: {
                  x: 400, // horizontal axis - can be a number or a string indicating unity. eg: '2em'
                  y: 300 // vertical axis - can be a number or a string indicating unity. eg: '2em'
                },
            style: {
              background: "red",
            }
          }).showToast();
        }
        else
        {

        
         navigate("/teacher")
      
         //-----------------to blank the textbox after submitting-----------
       setAdd({  
        name:"",
        joining_date:"",
        mobile:"",
        email:"",
        address:"",
        password:""
    })
    Toastify({
      text: "Successfully added....",
      duration: 5000,
      newWindow: true,
      close: true,
      stopOnFocus: true, // Prevents dismissing of toast on hover
      offset: {
            x: 400, // horizontal axis - can be a number or a string indicating unity. eg: '2em'
            y: 300 // vertical axis - can be a number or a string indicating unity. eg: '2em'
          },
      style: {
        background: "green",
      }
    }).showToast();
  }
  })
        }
    return(
        <div >
        <NavbarHeader/>
 <Container>
      <Row style={{padding:"40px"}}>
        <Col lg={4}/>
        <Col lg={4}  className="login_Box" >
            <h3>Add Teachers...</h3>
        <Form  method="post" onSubmit={Add}>
  <Form.Group className="mb-3" >
    {/* <Form.Label>Email address</Form.Label> */}
    <Form.Control type="text" placeholder="Name"  name="name" onChange={handleChange} value={add.name} required/>
  </Form.Group>
  <Form.Group className="mb-3" >
    {/* <Form.Label>Email address</Form.Label> */}
    <Form.Control type="date" placeholder="Name" name="joining_date"  onChange={handleChange} value={add.joining_date} required />
  </Form.Group>
  <Form.Group className="mb-3" >
    {/* <Form.Label>Email address</Form.Label> */}
    <Form.Control type="number" placeholder="Mobile" name="mobile"  onChange={handleChange} value={add.mobile} required />
  </Form.Group>
  <Form.Group className="mb-3" >
    {/* <Form.Label>Email address</Form.Label> */}
    <Form.Control type="email" placeholder="Email" name="email"  onChange={handleChange}  value={add.email} required />
  </Form.Group>
  <Form.Group className="mb-3" >
    {/* <Form.Label>Email address</Form.Label> */}
    <Form.Control as="textarea"  placeholder="Address" name="address" onChange={handleChange} value={add.address}  required/>
  </Form.Group>
  <Form.Group className="mb-3" controlId="formBasicPassword">
    {/* <Form.Label>Password</Form.Label> */}
    <Form.Control type="password" placeholder="Password" name="password" onChange={handleChange} value={add.password} required/>
  </Form.Group>
 
  <Button variant="primary" type="submit" id="submit" >
    Submit
  </Button>
  {/* <p> <Link to="/Login">Already Have an account</Link></p> */}
</Form>
        </Col>
        <Col lg={4}/>
      </Row>
    </Container>
 
 <Paper id="footer">Kerala Public School&copy;Pvt Ltd</Paper>
     </div>
       
    )
}