import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Grid, InputAdornment, TextField } from "@material-ui/core";
import { Search } from "@material-ui/icons";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { Col, Container, Form, Row, Table } from "react-bootstrap";
import Toastify from "toastify-js";
import NavbarHeader from "../Navbar/Navbar";
export default function StudentsDetails(){
    const [data,setData]=useState([])
    const [id,setId]=useState({id:""})
    const [open, setOpen] = React.useState(false);
    const [search,setSearch]=useState("")
    const searchChange=(e)=>{
        console.log(e.target.value);
        setSearch(e.target.value);
    }
     const [add,setAdd]=useState({
       adm_no:"",
         english:"",
         maths:"",
         computerscience:"",
         malayalam:""
        })
        const handleClickOpen = (e) => {
          setId(e)
           setOpen(true);
         };
       
         const handleClose = () => {
           setOpen(false);
         };
        useEffect(()=>{
          axios.get("/api/teacher/view").then((response)=>{
              console.log("studentsdataaa"+JSON.stringify(response.data))
              setData(response.data)
          })
      },[])
        const handleChange=(e)=>{
            console.log(e.target.value);
            const {name,value}=e.target;
             setAdd({...add,[name]:value})
            }
            const Add=(e)=>{
                e.preventDefault() 
                
                axios.post("/api/teacher/markadd",{
                  
                        adm_no:id,
                        english:add.english,
                        maths:add.maths,
                        computerscience:add.computerscience,
                        malayalam:add.malayalam,
                }).then((response)=>{
                  console.log("response is "+JSON.stringify(response));
                  if(response.data=="error"){
                    Toastify({
                      text: "This student mark has already added....",
                      duration: 5000,
                      newWindow: true,
                      close: true,
                      stopOnFocus: true, // Prevents dismissing of toast on hover
                      offset: {
                            x: 400, // horizontal axis - can be a number or a string indicating unity. eg: '2em'
                            y: 300 // vertical axis - can be a number or a string indicating unity. eg: '2em'
                          },
                      style: {
                        background: "red",
                      }
                    }).showToast();
                  }
                  else{
                    setAdd({
                      adm_no:"",
                      english:"",
                      maths:"",
                      computerscience:"",
                      malayalam:""
                    })
                    Toastify({
                      text: "Successfully added....",
                      duration: 5000,
                      newWindow: true,
                      close: true,
                      stopOnFocus: true, // Prevents dismissing of toast on hover
                      offset: {
                            x: 400, // horizontal axis - can be a number or a string indicating unity. eg: '2em'
                            y: 300 // vertical axis - can be a number or a string indicating unity. eg: '2em'
                          },
                      style: {
                        background: "green",
                      }
                    }).showToast();
                    // alert("mark added")
                    handleClose();
                  
                  }
                })
                
               
        }
   
    return(
        <div>
            <NavbarHeader/>
            <Container>
              <Row>
                <Col lg={1}/>
                <Col>
                <h2 style={{fontFamily:"monospace"}}>Students Details</h2>
                <TextField style={{width:"100%",border:"1px solid black"}}
           // className={classes.text}
               id="filled-helperText"
               label="Search Students"
               variant="filled"
               onChange={searchChange}
               value={search}
               InputProps={{
                 startAdornment: (
                   <InputAdornment position="start">
                     <Search
                     //  className={classes.search} 
                      />
                   </InputAdornment>
                 ),
               }}>
             </TextField>
            <Table striped bordered hover  style={{backgroundColor:"aliceblue"}}>
  <thead  style={{backgroundColor:"lightblue"}}>
    <tr>
      <th>Admission Number</th>
      <th>Name</th>
      <th>Mark</th>
    </tr>
  </thead>
  <tbody>
      {  data.filter((val)=>{
       if(val.name.toLowerCase().includes(search.toLowerCase()))
      {
        return val
      }
    })
      .map((details)=>(

    //  const admission=details.adm_no;
         <tr>
         <td>{details.adm_no}</td>
         <td>{details.name}</td>
         <td>
         <Button variant="contained" color="info" href="#contained-buttons" >
               <Button variant="outlined" color="primary" onClick={()=>{handleClickOpen(details.adm_no)}}>
        Mark Upload
      </Button>
               <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="draggable-dialog-title"
      >
        <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
        Enter the Marks of Different Subjects..
        </DialogTitle>
        <DialogContent>
        
        <TextField
          id="outlined-helperText"
          label="Admission Number"
          variant="filled"
          size="small"
          margin="dense" value={id} name="adm_no" 
          fullWidth
        />
        <TextField
          id="outlined-helperText"
          label="English"
          variant="filled"
          size="small"
          margin="dense" onChange={handleChange} value={add.english} name="english"
          fullWidth
        />
         <TextField
          id="outlined-helperText"
          label="Maths"
          variant="filled"
          margin="dense" onChange={handleChange} value={add.maths} name="maths"
          fullWidth
        />
         <TextField
          id="outlined-helperText"
          label="Computer Science"
          variant="filled"
          margin="dense" onChange={handleChange} value={add.computerscience} name="computerscience"
          fullWidth
        />
         <TextField
          id="outlined-helperText"
          label="Malayalam"
          variant="filled"
          margin="dense"
          fullWidth onChange={handleChange} value={add.malayalam} name="malayalam"
        />
        <div>
        <Button variant="contained" color="primary"  id="submit" onClick={Add}>
              Add
            </Button>
        </div>

        </DialogContent>
      </Dialog>
            </Button>
         </td>

       </tr>
      ))}
   
  
  </tbody>
</Table>
                </Col>
                <Col lg={1}/>
              </Row>
            </Container>
          
        </div>
    )
}