import { Button, Paper } from '@material-ui/core';
import { useState } from 'react';
import { Container, Navbar,Nav, Form, Row, Col } from "react-bootstrap";
import axios from 'axios';
import { Link, useNavigate } from 'react-router-dom';
import NavbarHeader from '../Navbar/Navbar';
import "toastify-js/src/toastify.css"
import Toastify from 'toastify-js'
export default function Register(){
    let navigate = useNavigate();
    const [add,setAdd]=useState({
        name:"",
        email:"",
        mobile:"",
        password:""
    })
    const handleChange=(e)=>{
        console.log(e.target.value);
        const {name,value}=e.target;
         setAdd({...add,[name]:value})
        }
        const Add=(e)=>{
            e.preventDefault() 
            axios.post("/api/",{
                name:add.name,
                email:add.email,
                mobile:add.mobile,
                password:add.password
            })
         .then((response)=>{
           console.log("backend data"+JSON.stringify(response.data))
           if(response.data=="error"){
            Toastify({
              text: "User with same email exists....",
              duration: 5000,
              newWindow: true,
              close: true,
              stopOnFocus: true, // Prevents dismissing of toast on hover
              offset: {
                    x: 400, // horizontal axis - can be a number or a string indicating unity. eg: '2em'
                    y: 300 // vertical axis - can be a number or a string indicating unity. eg: '2em'
                  },
              style: {
                background: "red",
              }
            }).showToast();
           }
           else
           {
            setAdd({  name:"",
            email:"",
            mobile:"",
            password:""})
            navigate("/registerdata")
           }
          })
         //-----------------to blank the textbox after submitting-----------
   
       
        }
    return(
        <div>
        <NavbarHeader/>
 <Container>
      <Row>
        <Col lg={4}/>
        <Col lg={4}  className="login_Box">
        <Form  method="post">
  <Form.Group className="mb-3" >
    {/* <Form.Label>Email address</Form.Label> */}
    <Form.Control type="text" placeholder="Name"  name="name" onChange={handleChange} value={add.name}/>
  </Form.Group>
  <Form.Group className="mb-3" >
    {/* <Form.Label>Email address</Form.Label> */}
    <Form.Control type="email" placeholder="email"name="email"  onChange={handleChange} value={add.email} />
  </Form.Group>
  <Form.Group className="mb-3" >
    {/* <Form.Label>Email address</Form.Label> */}
    <Form.Control type="number" placeholder="Mobile" name="mobile" onChange={handleChange} value={add.mobile} />
  </Form.Group>

 
  <Form.Group className="mb-3" controlId="formBasicPassword">
    {/* <Form.Label>Password</Form.Label> */}
    <Form.Control type="password" placeholder="Password" name="password" onChange={handleChange} value={add.password}/>
  </Form.Group>
 
  <Button variant="primary" type="submit" onClick={Add}>
    Submit
  </Button>
  <p> <Link to="/Login">Already Have an account</Link></p>
</Form>
        </Col>
        <Col lg={4}/>
      </Row>
    </Container>
 
 <Paper id="footer">Kerala Public School&copy;Pvt Ltd</Paper>
     </div>
       
    )
}