import { Button, Card, CardGroup, Col, Container, Row } from "react-bootstrap";
import './Feature.css'
import SchoolRoundedIcon from '@material-ui/icons/SchoolRounded';
import MenuBookIcon from '@material-ui/icons/MenuBook';
import SportsHandballRoundedIcon from '@material-ui/icons/SportsHandballRounded';
export default function Feature(){
    return(
        <div>
            <Container id="feature_container">
                <Row>
                    <Col lg={2}></Col>
                    <Col lg={8}>
                        <h1 id="feature_list">FEATURE LIST</h1>
                        <hr style={{color:"blue",height:"3px"}}></hr>
                        <p id="feature_desc">High expectations for students and staff.  It is common knowledge that students will generally live up to your expectations.  
                        When they know that teachers genuinely care about them and believe in their ability, they work harder and perform better.</p>
                    </Col>
                    <Col lg={2}></Col>
                </Row>
                <Row>
                    <Col lg={1}></Col>
                    <Col><CardGroup>
                    <Card style={{ width: '18rem',border:"none" }}>
                      <Card.Body>
                          <SchoolRoundedIcon id="icon"/>
                   <Card.Title id="card_title">Better Results</Card.Title>
                    <Card.Text>
                   Some quick example text to build on the card title and make up the bulk of
                   the card's content.
                </Card.Text>
               </Card.Body>
             </Card>
             <Card style={{ width: '18rem',border:"none" }}>
                      <Card.Body>
                          <MenuBookIcon id="icon"/>
                   <Card.Title id="card_title">Attractive Syllabus</Card.Title>
                    <Card.Text>
                   Some quick example text to build on the card title and make up the bulk of
                   the card's content.
                </Card.Text>
               </Card.Body>
             </Card>
             <Card style={{ width: '18rem' ,border:"none"}}>
                      <Card.Body>
                          <SportsHandballRoundedIcon id="icon"/>
                   <Card.Title id="card_title">Sports Participation</Card.Title>
                    <Card.Text>
                   Some quick example text to build on the card title and make up the bulk of
                   the card's content.
                </Card.Text>
               </Card.Body>
             </Card>
            </CardGroup>
                    </Col>
                    <Col lg={1}></Col>
                </Row>
            </Container>
        </div>
    )
}