import axios from "axios"
import React, { useEffect, useState } from "react"
import { Button, Col, Container, Row, Table } from "react-bootstrap"
import NavbarHeader from "../Navbar/Navbar"
import EditIcon from '@material-ui/icons/Edit';
import CloseIcon from '@material-ui/icons/Close';
import { Dialog, DialogContent, DialogTitle, TextField } from "@material-ui/core";
import Toastify from "toastify-js";
export default function StudentMarkList(){
    const [datas,setDatas]=useState([])
    const [data,setData]=useState([])
    const [id,setId]=useState({id:""})
    const [open, setOpen] = React.useState(false);
    
    const [add,setAdd]=useState({
        adm_no:"",
          english:"",
          maths:"",
          computerscience:"",
          malayalam:""
         })
         const handleClickOpen = (e) => {
           setId(e)
            setOpen(true);
            console.log("iiiiiiiiiiii"+e);
            axios.get(`/api/teacher/edit/${e}`).then((editData) => {
                console.log("edit data: " + JSON.stringify(editData.data));
                setAdd(editData.data)
              })
          };
        
          const handleClose = () => {
            setOpen(false);
          };
          const handleChange=(e)=>{
            console.log(e.target.value);
            const {name,value}=e.target;
             setAdd({...add,[name]:value})
            }
    useEffect(()=>{
        axios.get("/api/teacher/marklist").then((response)=>{
            console.log(JSON.stringify(response.data));
            setDatas(response.data.Stdmark)
            console.log(datas);
        })
    })
    function deleteRecord(id) {
    
        fetch(`/api/teacher/delete/${id}`, {
            method:"DELETE"
        });
        setData(data.filter((val)=>{
          return val._id!=id
        }
        ));
      }
      const update=(e)=>{
        e.preventDefault() 
        axios.put(`/api/teacher/update/${id}`,{
            adm_no:add.adm_no,
            english:add.english,
            maths:add.maths,
            computerscience:add.computerscience,
            malayalam:add.malayalam,
           
        })
     .then((response)=>{console.log("backend data"+response)
   
    })
    Toastify({
      text: "Successfully updated....",
      duration: 5000,
      newWindow: true,
      close: true,
      stopOnFocus: true, // Prevents dismissing of toast on hover
      offset: {
            x: 400, // horizontal axis - can be a number or a string indicating unity. eg: '2em'
            y: 300 // vertical axis - can be a number or a string indicating unity. eg: '2em'
          },
      style: {
        background: "green",
      }
    }).showToast();
    setOpen(false);
// navigate("/student")
    }
    return(
        <div>
            <NavbarHeader/>
            <Container>
                <Row>
                    <Col lg={1}/>
                    <Col>
                    <h2 style={{fontFamily:"monospace"}}>Student's Result</h2>
                    <Table striped bordered hover style={{backgroundColor:"aliceblue"}}>
  <thead   style={{backgroundColor:"lightblue"}}>
    <tr>
        <th>Admission Numer</th>
      <th>English</th>
      <th>Maths</th>
      <th>Computer Science</th>
      <th>Malayalam</th>
      <th>Actions</th>
    </tr>
  </thead>
  <tbody>
      {datas.map((mark)=>(
              <tr>
                  <td>{mark.adm_no}</td>
                  
              <td>{mark.english}</td>
              <td>{mark.maths}</td>
              <td>{mark.computerscience}</td>
              <td>{mark.malayalam}</td>
              <td>
                  <div>
               {/* <EditIcon 
               onClick={() => {
                updateRecord(mark._id);
                 }} /> */}
                 
                    <Button variant="outlined" color="primary"onClick={()=>{handleClickOpen(mark._id)}}>
        <EditIcon/>
      </Button>
               <Dialog style={{height:"500px"}}
        open={open}
        onClose={handleClose}
        aria-labelledby="draggable-dialog-title"
      >
        <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
        Enter the Marks of Different Subjects..
        </DialogTitle>
        <DialogContent>
        
        <TextField
          id="outlined-helperText"
          label="Admission Number"
          variant="filled"
          size="small"
          margin="dense" value={add.adm_no} name="adm_no" 
          fullWidth
        />
        <TextField
          id="outlined-helperText"
          label="English"
          variant="filled"
          size="small"
          margin="dense" onChange={handleChange} value={add.english} name="english"
          fullWidth
        />
         <TextField
          id="outlined-helperText"
          label="Maths"
          variant="filled"
          margin="dense" onChange={handleChange} value={add.maths} name="maths"
          fullWidth
        />
         <TextField
          id="outlined-helperText"
          label="Computer Science"
          variant="filled"
          margin="dense" onChange={handleChange} value={add.computerscience} name="computerscience"
          fullWidth
        />
         <TextField
          id="outlined-helperText"
          label="Malayalam"
          variant="filled"
          margin="dense"
          fullWidth onChange={handleChange} value={add.malayalam} name="malayalam"
        />
        <div>
        <Button variant="contained" color="primary"  onClick={update} id="submit">
             Update
            </Button>
        </div>

        </DialogContent>
      </Dialog>
               <CloseIcon onClick={() => {
              deleteRecord(mark._id);
               }}/></div>
               </td>
          </tr>
      ))}
     
  </tbody>
  </Table>
                    </Col>
                    <Col lg={1}/>
                </Row>
            </Container>
    
        </div>
    )
}
