import { Col, Collapse, Container, Row } from "react-bootstrap";
import './Contact.css'
export default function Contact(){
    return(
        <div  id="contact_container">
            <div id="sub_div">
           <Container>
               <Row>
                   <Col lg={2}></Col>
                   <Col lg={8}>
                       <h2 id="contact_heading">CONTACT US</h2>
                       <hr style={{color:"white",height:"3px"}}></hr>
                       <div id="address">
                       <h4>234/80 -UFG , New Street,</h4>
                       <h5>Kerala</h5>
                       <h5>Call: + 67-098-907-1269 / 70 / 71</h5>
                       <h5>
                        Email: info@yourdomain.com</h5>
                        </div>
                   </Col>
                   <Col lg={2}></Col>
               </Row>
           </Container>
           </div>
        </div>
    )
}