import './Login.css'
import {Paper } from '@material-ui/core';
import { Container, Navbar,Nav, Form, Row, Col, Button } from "react-bootstrap";
import { Link, useNavigate ,useParams} from 'react-router-dom';
import axios from 'axios'
import { useEffect, useState } from 'react';
import NavbarHeader from '../Navbar/Navbar';
export default function NewPassword(){
  const [password,setPassword]=useState({
  password:""
})
const navigate=useNavigate();

const {token}=useParams()
const handleChange=(e)=>{
    console.log(e.target.value);
    const {name,value}=e.target;
    setPassword({...password,[name]:value})
    }
    const reset=(e)=>{
      axios.post(`/api/confirmPassword/${token}`,{
          password:password.password,
          // confirmPassword:password.confirmPassword
      }).then((response)=>{
          console.log("New Password"+response);
      })
      navigate("/registerdata")
    }
  return(
    <div>
        <NavbarHeader/>
 <Container>
      <Row>
        <Col lg={4}/>
        <Col lg={4}  className="login_Box">
        <Form>
  <Form.Group className="mb-3" controlId="formBasicEmail">
    {/* <Form.Label>Email address</Form.Label> */}
    <Form.Control type="password" placeholder="Enter your new password" name="password" onChange={handleChange} value={password.password}/>
  </Form.Group>
  {/* <Form.Group className="mb-3" controlId="formBasicEmail">

    <Form.Control type="password" placeholder="Confirm Password" name="password" onChange={handleChange} value={password.confirmPassword}/>
  </Form.Group> */}
  <Button variant="primary" type="submit" onClick={reset}>
    Reset
  </Button>
</Form>
        </Col>
        <Col lg={4}/>
      </Row>
    </Container>
 <Paper id="footer">Kerala Public School&copy;Pvt Ltd</Paper>
     </div>
   

  )
}