
import { Button, InputAdornment, TextField } from "@material-ui/core";
import { Col, Container, Row, Table } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import axios from 'axios'
import { Search } from '@material-ui/icons';
import { useEffect, useState } from "react";
import NavbarHeader from "../Navbar/Navbar";
import EditIcon from '@material-ui/icons/Edit';
import CloseIcon from '@material-ui/icons/Close';
export default function StudentsList(){
  const [data,setData]=useState([])
  const [search,setSearch]=useState("")
  const searchChange=(e)=>{
      console.log(e.target.value);
      setSearch(e.target.value);
  }
  let navigate = useNavigate();
  const student=(e)=>{
     navigate("/studentRegister")
  }
// useEffect(()=>{
  axios.get("/api/admin/studentList").then((studentsData)=>{
  // console.log("students data"+JSON.stringify(studentsData.data[1].name));
  setData(studentsData.data)
// },[])
})
function deleteRecord(id) {
    
  fetch(`/api/admin/Sdelete/${id}`, {
      method:"DELETE"
  });
  setData(data.filter((val)=>{
    return val._id!=id
  }
  ));
}
const updateRecord=(id)=>{
   navigate(`/editStudent/${id}`)
}

    return(
        <div>
           <NavbarHeader/>
          <Container>
            <Row>
              <Col lg={1}/>
              <Col>
              <h2  style={{fontFamily:"monospace"}}>Student's List</h2>
           
           <TextField style={{width:"100%",border:"1px solid black"}}
           // className={classes.text}
               id="filled-helperText"
               label="Search Students"
               variant="filled"
               className="search_bar"
               onChange={searchChange}
               value={search}
               InputProps={{
                 startAdornment: (
                   <InputAdornment position="start">
                     <Search
                     //  className={classes.search} 
                      />
                   </InputAdornment>
                 ),
               }}>
             </TextField>
           <Table striped bordered hover  style={{backgroundColor:"aliceblue"}}>
 <thead  style={{backgroundColor:"lightblue"}}>
   <tr>
     <th>Admission Number</th>
     <th>Name</th>
     <th>Standard</th>
     <th>Email</th>
     <th>Address</th>
     <th>Actions</th>

   </tr>
 </thead>
 <tbody>
   {
      data.filter((val)=>{
       if(val.name.toLowerCase().includes(search.toLowerCase()))
      {
        return val
      }
    })
     .map((u)=>(
        <tr>
       <td>{u.adm_no}</td>
       <td>{u.name}</td>
       <td>{u.class}</td>
       <td>{u.email}</td>
       
       <td>{u.address}</td>
       <td><div>
              <EditIcon 
              onClick={() => {
               updateRecord(u._id);
                }} />
              <CloseIcon onClick={() => {
             deleteRecord(u._id);
              }}/></div></td>
     </tr> 
     ))}
  
 </tbody>
</Table>
           <Button variant="contained" color="primary" href="#contained-buttons"  onClick={student}>
             Add Student..
          </Button></Col>
              <Col lg={1}/>
            </Row>
          </Container>
         
           
        </div>
    )
}