import './App.css';
import {
  BrowserRouter,
  Routes,
  Route
} from "react-router-dom";
import Admin from './Components/admin/AdminHome';
import Register from './Components/Register/Register';
import Login from './Components/Login/Login';
import Home from './Components/Home/Home';
import StudentRegister from './Components/admin/studentRegister/studentRegister';
import TeacherRegister from './Components/admin/teacherRegister/teacherRegister';
import StudentsList from './Components/admin/StudentsList';
import TeachersList from './Components/admin/TeachersList';
import EditTeacherRegister from './Components/admin/EditTeacher';
import Student from './Components/Student/student';
import Teacher from './Components/teacher/Teacher';
import EditStudent from './Components/admin/EditStudent';
import StudentsDetails from './Components/teacher/studentsList';
import TeacherFeedback from './Components/teacher/teacherfeedback';
import StudentMarkList from './Components/teacher/studentMarklist';
import ResetPassword from './Components/Login/ResetPassword';
import NewPassword from './Components/Login/NewPassword';
function App() {
  return (
    <div className="App">
  <BrowserRouter>
     <Routes>
       <Route exact path="/" element={<Home/>}>  </Route>
       <Route path="/Login" element={<Login/>}>  </Route>
       <Route path="/Register" element={<Register/>}></Route>
       <Route path="/registerdata" element={<Login/>}></Route>
       <Route path="/admin" element={<Admin/>}></Route>
       <Route path="/student" element={<StudentsList/>}></Route>
       <Route path="/teacher" element={<TeachersList/>}></Route>
       <Route path="/studentRegister" element={<StudentRegister/>}></Route>
       <Route path="/teacherRegister" element={<TeacherRegister/>}></Route>
       <Route path="/editTeacher/:id"  element={<EditTeacherRegister/>}></Route>
       <Route path="/editStudent/:id"  element={<EditStudent/>}></Route>
       <Route path="/studentdash" element={<Student/>}>  </Route>
       <Route path="/teacherdash" element={<Teacher/>}>  </Route>
       <Route path="/feedback" element={<TeacherFeedback/>}>  </Route>
       <Route path="/teacherdashstudent" element={<StudentsDetails/>}>  </Route>
       <Route path="/marklist" element={<StudentMarkList/>}>  </Route>
       <Route path="/reset" element={<ResetPassword/>}>  </Route>
       <Route path="/newPassword/:token" element={<NewPassword/>}>  </Route>
     </Routes>
     </BrowserRouter>
   </div>    
  );
}

export default App;
