import { Button, Paper } from '@material-ui/core';
import { useEffect, useState } from 'react';
import { Container, Navbar, Nav, Form, Row, Col } from "react-bootstrap";
import axios from 'axios';
import { Link, useNavigate, useParams } from 'react-router-dom';
import NavbarHeader from '../Navbar/Navbar';
import Toastify from 'toastify-js';
export default function EditTeacherRegister() {
  const [add, setAdd] = useState({
    name: "",
    joining_date: "",
    mobile: "",
    email: "",
    address: "",
    username: "",
    password: ""
  })
  let navigate = useNavigate();
  const  { id }= useParams();
  useEffect(() => {
    axios.get(`/api/admin/Tedit/${id}`).then((editData) => {
      console.log("edit data: " + JSON.stringify(editData.data));
      setAdd(editData.data)
    })
  },[])
  const handleChange = (e) => {
    console.log(e.target.value);
    const { name, value } = e.target;
    setAdd({ ...add, [name]: value })
  }
  const update=(e)=>{
      e.preventDefault() 
      axios.put(`/api/admin/Tupdate/${id}`,{
          name:add.name,
          joining_date:add.joining_date,
          mobile:add.mobile,
          email:add.email,
          address:add.address,
          // username:add.username,
          // password:add.password
      })
   .then((response)=>{console.log("backend data"+response.data.data)
   
    })
    Toastify({
      text: "Successfully updated....",
      duration: 5000,
      newWindow: true,
      close: true,
      stopOnFocus: true, // Prevents dismissing of toast on hover
      offset: {
            x: 400, // horizontal axis - can be a number or a string indicating unity. eg: '2em'
            y: 300 // vertical axis - can be a number or a string indicating unity. eg: '2em'
          },
      style: {
        background: "green",
      }
    }).showToast();
   navigate("/teacher")
    }
  return (
    <div>
      <NavbarHeader />
       <div  style={{padding:"50px"}}>
      <Container>
        <Row>
          <Col lg={4} />
          <Col lg={4} className="login_Box">
            <h3>Edit Teacher's data</h3>
            <Form method="post">
              <Form.Group className="mb-3" >
                {/* <Form.Label>Email address</Form.Label> */}
                <Form.Control type="text" placeholder="Name" name="name" onChange={handleChange} value={add.name} />
              </Form.Group>
              <Form.Group className="mb-3" >
                {/* <Form.Label>Email address</Form.Label> */}
                <Form.Control type="date" placeholder="Name" name="joining_date" onChange={handleChange} value={add.joining_date} />
              </Form.Group>
              <Form.Group className="mb-3" >
                {/* <Form.Label>Email address</Form.Label> */}
                <Form.Control type="number" placeholder="Mobile" name="mobile" onChange={handleChange} value={add.mobile} />
              </Form.Group>
              <Form.Group className="mb-3" >
                {/* <Form.Label>Email address</Form.Label> */}
                <Form.Control type="email" placeholder="Email" name="email" onChange={handleChange} value={add.email} />
              </Form.Group>
              <Form.Group className="mb-3" >
                {/* <Form.Label>Email address</Form.Label> */}
                <Form.Control as="textarea" placeholder="Address" name="address" onChange={handleChange} value={add.address} />
              </Form.Group>

              <Button variant="primary" type="submit" id="submit" onClick={update} >
                Update
              </Button>
              {/* <p> <Link to="/Login">Already Have an account</Link></p> */}
            </Form>
          </Col>
          <Col lg={4} />
        </Row>
      </Container>
</div>
      <Paper id="footer">Kerala Public School&copy;Pvt Ltd</Paper>
    </div>

  )
}