
import { Button, InputAdornment, TextField } from "@material-ui/core";
import axios from "axios";
import { useEffect, useState } from "react";
import { Col, Container, Row, Table } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import EditIcon from '@material-ui/icons/Edit';
import CloseIcon from '@material-ui/icons/Close';
import NavbarHeader from "../Navbar/Navbar";
import { Search } from "@material-ui/icons";
export default function TeachersList(){
    const [data,setData]=useState([])
    const [search,setSearch]=useState("")
    let navigate = useNavigate();
    const searchChange=(e)=>{
      console.log(e.target.value);
      setSearch(e.target.value);
  }
  const teacher=(e)=>{
     navigate("/teacherRegister")   
  }
  useEffect(()=>{
    axios.get("/api/admin/teacherList").then((teachersData)=>{
    setData(teachersData.data)
  })
  },[])
  function deleteRecord(id) {
    
    fetch(`/api/admin/Tdelete/${id}`, {
        method:"DELETE"
    });
    setData(data.filter((val)=>{
      return val._id!=id
    }
    ));
  }
 const updateRecord=(id)=>{
     navigate(`/editTeacher/${id}`)
 }
    return(
        <div >
            <NavbarHeader/>
            <Container>
              <Row>
                <Col lg={1}/>
                <Col>
                <h2 style={{fontFamily:"monospace"}}>Teacher's List</h2>
                <TextField style={{width:"100%",border:"1px solid black"}}
                id="filled-helperText"
                className="search_bar"
                label="Search Teachers"
                variant="filled"
                onChange={searchChange}
                value={search}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <Search
                       />
                    </InputAdornment>
                  ),
                }}>
              </TextField>
            <Table striped bordered hover style={{backgroundColor:"aliceblue"}}>
  <thead style={{backgroundColor:"lightblue"}}>
    <tr>
      <th>Name</th>
      <th>Joining date</th>
      <th>Email</th>
      <th>Address</th>
      <th>Actions</th>
    </tr>
  </thead>
  <tbody>
      {   data.filter((val)=>{
        if(val.name.toLowerCase().includes(search.toLowerCase()))
       {
         return val
       }
     })
      .map((u)=>(
           <tr>
           <td>{u.name}</td>
           <td>{u.joining_date}</td>
           <td>{u.email}</td>
           <td>{u.address}</td>
           <td><div>
               <EditIcon 
               onClick={() => {
                updateRecord(u._id);
                 }} />
               <CloseIcon onClick={() => {
              deleteRecord(u._id);
               }}/></div></td>
         </tr>
      ))}
  
  </tbody>
</Table>
            <Button variant="contained" color="primary" href="#contained-buttons"  onClick={teacher}  id="submit">
              Add Teachers..
           </Button>
                </Col>
                <Col lg={1}/>
              </Row>
            </Container>
            
        </div>
    )
}